.form-box {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    width: 100%;
    max-width: 400px;
    width: 100%;
    min-width: 245px;
    margin: 15px auto;
}

.form-box input {
    padding: 10px;
    margin: 10px 0;
    font-size: 22px;
    border: 10px solid rgba(255, 255, 255, .5);
    -webkit-box-shadow: 0 2px 10px rgba(0, 0, 0, .25) inset,
    0 2px 10px rgba(0, 0, 0, .25);
            box-shadow: 0 2px 10px rgba(0, 0, 0, .25) inset,
    0 2px 10px rgba(0, 0, 0, .25);
    border-radius: 10px;
    background: rgba(255, 255, 255, .5);
    outline: none;
    color: #333;
    width: 85%;
    max-width: 360px;
}

.btn {
    display: block;
    margin: 0 auto;
    width: 80%;
    height: 40px;
    font-size: 22px;
    cursor: pointer;
    border: none;
    -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}


.form-box button {
    color: #34495e;
    line-height: 1.2;
    position: relative;
    padding: 0 14px;
    text-transform: uppercase;
    margin-top: 20px;
}

.form-box button:after {
    content: "";
    height: 100%;
    min-width: 4px;
    background: #34495e;
    position: absolute;
    left: 0;
    bottom: 0;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}

.form-box button:hover:after {
    min-width: 100%;
    background: #95a5a6;
    opacity: .35;
}


.Form__style {
    border: 3px dashed #169ed1;
    border-radius: 20px;
    background: #e5f2ff;
    width: 60%;
    min-width: 245px;
    max-width: 500px;
    height: auto;
    margin: 40px auto;
    padding: 0 20px;
    text-align: center;
}

.Form_img {
    width: 100%;
    max-width: 380px;
    margin: 0 auto !important;
    display: block;
}

.Form_p {
    text-align: center;
    font-size: 25px;
    padding: 10px 0;
}

.Form_pp {
    text-align: center;
    font-size: 18px;
    padding: 10px 0;
    line-height: 24px !important;
    margin: 5px 5px !important;
}

.ANDR {
    font-size: 60px;
}

.tyfororder {
    text-align: center !important;
}

@media screen and (max-width: 400px) {
    #content img {
        width: 85%;
    }

    .Form_pp {
        font-size: 16px;
        text-align: center !important;
        text-indent: 0px !important;
        margin: 0px 15px !important;
    }

    .form-box input {
        font-size: 18px;
    }

    .btn {
        font-size: 18px;
    }

    .Form__style {
        min-width: 265px;
        padding: 0 10px;
        width: calc(100% - 20px);
    }
    
    .Form_p {
        font-size: 24px;
        text-align: center !important;
        text-indent: 0px !important;
    }
}



